
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { validateNotNull, validateUpload } from 'util/rules'
import Scroll from 'assembly/scroll/scroll'
import Uploader from 'assembly/uploader/uploader'

export default {
data () {
return {
baseUrl: process.env.FMS_SERVICE_IP,
type: '',
typeName: '请选择款项用途',
balance: '',
deposit: '',
commitTime: '',
bank: '',
bankName: '请选择收款银行',
remark: '',
credential: [],
credential1: '',
credential2: '',
typeSelected: false,
bankSelected: false,
typeMenu: {
'8796120907867': '货款账户',
'8796120940635': '保证金账户',
'8796121071707': '同时转货款账户和保证金账户'
},
bankMenu: {
bank1: '建设银行(7455)',
bank2: '工商银行(25204)'
},
inited: false,
endDate: new Date().format('yyyy-MM-dd'),
orderNo: '', // 从订单支付过来的orderNo
deposited: 0 
}
},
watch: {
type (newValue) {
let thiz = this
if (this.deposited > 0 && (newValue === '8796120940635' || newValue === '8796121071707')) {
this.$vux.confirm.show({
content: '你的账号已经有保证金' + this.deposited + '元，是否需要继续充值？',
onCancel () {
thiz.type = ''
thiz.typeName = '请选择款项用途'
},
onConfirm () {
}
})
}
}
},
methods: {
goBack () {
this.$router.go(-1)
},
showSelector () {
this.typeSelected = true
},
showBankSelector () {
this.bankSelected = true
},
setType (key, item) {
this.type = key
this.typeName = item
},
setBank (key, item) {
this.bankName = item
this.bank = item
},
beforeSend (file) {
if (validateUpload(this, file)) {
this.$vux.loading.show({
text: '图片上传中，请稍候'
})
return true
}
},
onFinish (res) {
this.$vux.loading.hide()
this.credential.push(res.model)
},
onError () {
this.$vux.loading.hide()
this.$vux.toast.text('上传失败，请重试~')
},
delImg (index) {
this.credential.splice(index, 1)
},
submit () {
if (this.inited) {
return
}
let flag = false
if (validateNotNull(this, this.type, '款项用途')) {
flag = (this.type === '8796120907867' && validateNotNull(this, this.balance, '货款金额')) || (this.type === '8796120940635' && validateNotNull(this, this.deposit, '保证金金额')) ||
(this.type === '8796121071707' && validateNotNull(this, this.balance, '货款金额') && validateNotNull(this, this.deposit, '保证金金额'))
}
if (!flag) {
return
}
if (validateNotNull(this, this.commitTime, '打款日期') && validateNotNull(this, this.bank, '特百惠收款银行') && validateNotNull(this, this.credential, '上传凭证')) {
let data = {
type: this.type,
commitTime: this.commitTime,
bank: this.bank,
remark: this.remark,
credential1: this.credential[0],
credential2: this.credential[1] ? this.credential[1] : ''
}
if (data.type !== '8796120907867') {
data.deposit = this.deposit
}
if (data.type !== '8796120940635') {
data.balance = this.balance
}
if (this.orderNo) {
data.orderNo = this.orderNo
}
this.$tupHttp(this, 'front/account/charge/offline', 'POST', data).then(res => {
this.inited = true
this.$vux.toast.text('提交成功，请等待客服审核~')
setTimeout(() => {
this.$router.go(-1)
}, 2000)
})
}
},
_getInfo () {
this.$tupHttp(this, 'front/user/info/v1').then(res => {
this.deposited = res.data.model.userAccount.deposit / 100
})
}
},
created () {
this._getInfo()
let amount = this.$route.query.amount
this.orderNo = this.$route.query.orderNo
if (amount) {
this.balance = amount
this.type = '8796120907867'
this.typeName = '货款账户'
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Datetime,
Cell,
Actionsheet,
XTextarea,
Scroll,
Uploader
}
}



